* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: "Cabin", Arial, sans-serif; }

.c-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 192px;
  padding: 15px;
  background-color: #383737; }
  @media only screen and (min-width: 376px) and (max-width: 768px) {
    .c-footer {
      height: 225px; } }
  @media only screen and (max-width: 375px) {
    .c-footer {
      height: 250px; } }
  .c-footer__menu {
    display: block;
    text-align: center; }
    .c-footer__menu-item {
      display: inline-block;
      color: #fff;
      font-size: 16px;
      line-height: 2.25;
      letter-spacing: 0.02rem;
      text-decoration: none; }
      .c-footer__menu-item:not(:last-child) {
        margin-right: 30px; }
      .c-footer__menu-item:hover {
        text-decoration: underline; }
  .c-footer .c-social {
    margin-top: 30px;
    text-align: center; }
    .c-footer .c-social-item {
      padding: 9px;
      border: 1px solid;
      border-radius: 999rem;
      outline: none;
      color: #fff;
      font-size: 16px;
      text-decoration: none; }
      .c-footer .c-social-item:not(:last-child) {
        margin-right: 20px; }
      .c-footer .c-social-item.c--facebook {
        border-color: #3162a2;
        background-color: #3162a2; }
        .c-footer .c-social-item.c--facebook:hover {
          border-color: #235493;
          background-color: #235493;
          cursor: pointer; }
      .c-footer .c-social-item.c--twitter {
        border-color: #2eb1eb;
        background-color: #2eb1eb; }
        .c-footer .c-social-item.c--twitter:hover {
          border-color: #209dd4;
          background-color: #209dd4;
          cursor: pointer; }
      .c-footer .c-social-item.c--dribbble {
        border-color: #f552b6;
        background-color: #f552b6; }
        .c-footer .c-social-item.c--dribbble:hover {
          border-color: #d43b99;
          background-color: #d43b99;
          cursor: pointer; }
      .c-footer .c-social-item.c--vimeo {
        border-color: #3498b9;
        background-color: #3498b9; }
        .c-footer .c-social-item.c--vimeo:hover {
          border-color: #2485a5;
          background-color: #2485a5;
          cursor: pointer; }
      .c-footer .c-social-item.c--google-plus {
        border-color: #f25c58;
        background-color: #f25c58; }
        .c-footer .c-social-item.c--google-plus:hover {
          border-color: #d24440;
          background-color: #d24440;
          cursor: pointer; }
  .c-footer__copyright {
    margin-top: 30px;
    text-align: center; }
    .c-footer__copyright-copy {
      color: #838282;
      font-size: 14px;
      line-height: 2.57;
      letter-spacing: 0.02rem; }
